.createTodoButton {
    background-color: #610AFA;
    box-shadow: 0px 5px 25px rgba(97, 208, 250, .5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 54px;
    width: 54px;
    z-index: 1;
    transform: rotate(0);
    transition: .5s ease;
}

.createTodoButton:hover {
    transform: rotate(224deg);
}